import { Component, AfterViewInit, OnInit } from '@angular/core';
import { MaisonsService } from '../maisons.service';
import { Maison } from '../maison';

declare var M: any;

@Component({
	selector: 'app-carousel',
	templateUrl: './carousel.component.html',
	styleUrls: ['./carousel.component.css']
})

export class CarouselComponent implements OnInit, AfterViewInit {

	selectedMaison: Maison;
    maisons: Maison[];

	constructor(private maisonsService: MaisonsService) { }
	// Au chargement de la page	
	ngOnInit() {
        this.maisonsService
        .getMaisons()
        .subscribe((data: Maison[]) => {
            this.maisons = data;
            this.selectedMaison = data[0];
        });
	}
	// Au chargement de la vue
	ngAfterViewInit() {
		// Eléments Materialize, doivent être initialisés après le chargement de la vue
		var elems = document.querySelectorAll('.carousel');
		var instances = M.Carousel.init(elems);
		var elems = document.querySelectorAll('.parallax');
		var instances = M.Parallax.init(elems);
	}
	// Au clic sur un blason
	onSelect(maison: Maison): void {
		this.selectedMaison = maison;
	}
}